.note {
    display: inline-block;
    &__call {
        @include meta;
        background: var(--color-background-alt);
        border-radius: 4px;
        cursor: pointer;
        display: inline;
        line-height: inherit;
        min-width: $minimum-accessible-size;
        line-height: inherit;
        display: block;
        padding-left: $spacing-2;
        padding-right: $spacing-2;
        padding-bottom: 0;
        text-align: center;
        user-select: none; }
    &__content {
        background: var(--color-text);
        color: var(--color-background);
        display: none;
        max-width: 100vw;
        padding: $spacing-3;
        position: absolute;
        width: 450px;
        z-index: 999;
        @include small; }
    &--active {
        .note__call {
            background: var(--color-text);
            color: var(--color-background);
            border-bottom-left-radius: 0;
            border-bottom-right-radius: 0; }
        .note__content {
            display: block; } }
    @include media-breakpoint-up(desktop) {
        position: relative;
        &__content {
            border-radius: 4px;
            top: pxToRem(25); }
        &--left {
            .note__content {
                left: 0;
                border-top-left-radius: 0; } }
        &--right {
            .note__content {
                right: 0;
                border-top-right-radius: 0; } } }
    @include media-breakpoint-down(desktop) {
        .note__content {
            left: 0;
            right: 0;
            margin-top: pxToRem(-$space-unit); } } }
