.block-posts {
    .top {
        margin-bottom: $spacing-4;
        a {
            @include icon(arrow-right, after, true);
            @include hover-translate-icon;
            text-decoration: none; } }
    .posts {
        @include grid(1);
        @include grid($block-posts-grid-columns, desktop); }
    article {
        @include author-and-time-side-to-side;
        .post-title {
            a {
                @include stretched-link;
                text-decoration: none; } }
        .post-content {
            > * + * {
                margin-top: $spacing-2; }
            .media {
                margin-top: 0; } } }

    &--grid {
        @include media-breakpoint-down(desktop) {
            article + article {
                margin-top: $spacing-5; } }
        @include in-page-with-sidebar {
            .grid {
                @include grid(2); } }
        @include in-page-without-sidebar {
            .grid {
                @include grid($block-posts-grid-columns);
                .media picture img {
                    width: 100%; } } } }
    &--large {
        .post {
            .more {
                @include icon(arrow-right, after, true); }

            // Masquer le point entre l'auteur et la date (mixin author-and-time-side-to-side)
            .post-author {
                p::before {
                    display: none; } }

            // Permet d'aligner correctement les différents line-height des différents composants du post meta
            .post-meta {
                display: block;
                line-height: 0; // Hack pour que les enfants de post-meta soient correctement
                > * {
                    margin-right: $spacing-3; }
                time {
                    vertical-align: baseline; }
                .post-author {
                    display: inline;
                    p {
                        display: inline; } } }
            .post-categories {
                display: inline;
                * {
                    display: inline-block; }
                li {
                    margin-right: $spacing-2; } }
            // Désactiver le ratio forcé de la configuration $article-media-aspect-ratio
            .media {
                &, img {
                    aspect-ratio: auto; } } }
        @include media-breakpoint-down(desktop) {
            .post {
                + .post {
                    margin-top: $spacing-4; }
                p[itemprop="articleBody"] {
                    margin-top: 0; } } }
        @include media-breakpoint-up(desktop) {
            .large {
                .post {
                    flex-direction: row;
                    gap: var(--grid-gutter);
                    + .post {
                        margin-top: $spacing-4; }
                    .post-title {
                        @include h2; }
                    p[itemprop="articleBody"] {
                        margin-top: $spacing-3; }
                    .post-categories {
                        li {
                            a {
                                display: inline; } } } } } }

        @include in-page-with-sidebar {
            .large {
                .post {
                    @include media-breakpoint-down(xl) {
                        flex-direction: column;
                        gap: unset;
                        p[itemprop="articleBody"] {
                            margin-top: $spacing-2; } }
                    @include media-breakpoint-up(xl) {
                        .media,
                        .post-content {
                            width: columns(4); } } } } }
        @include in-page-without-sidebar {
            .large {
                .post {
                    gap: var(--grid-gutter);
                    .media {
                        width: columns(6); }
                    .post-content {
                        width: columns(5); }
                    .post-title {
                        @include lead; } }
                p[itemprop="articleBody"] {
                    margin-top: calc(#{$spacing-3} + #{$spacing-2}); } } } }
    &--list {
        article {
            border-bottom: 1px solid var(--color-border);
            .media {
                background: none;
                margin: 0;
                &, img {
                    aspect-ratio: auto; } } }

        @include media-breakpoint-down(desktop) {
            article {
                position: relative;
                padding-bottom: $spacing-3;
                + article {
                    margin-top: $spacing-3; }
                .post-content {
                    display: flex;
                    flex-direction: column;
                    padding-top: $spacing-2;
                    .post-title {
                        margin-bottom: 0; }
                    .post-categories {
                        margin-bottom: 0; }
                    p[itemprop="articleBody"] {
                        margin-top: $spacing-1; } }
                .media {
                    order: -1;
                    max-width: 33%; } } }

        @include media-breakpoint-up(desktop) {
            article {
                display: flex;
                flex-direction: row; } }

        @include in-page-with-sidebar {
            article {
                padding-bottom: $spacing-3;
                + article {
                    margin-top: $spacing-3; }
                .media {
                    width: columns(2); }
                .post-content {
                    width: columns(6);
                    margin-left: var(--grid-gutter); } } }

        @include in-page-without-sidebar {
            article {
                border-bottom: 1px solid var(--color-border);
                gap: var(--grid-gutter);
                padding-bottom: $spacing-5;
                position: relative;
                .media {
                    width: columns(3); }
                .post-content {
                    width: columns(6); }
                + article {
                    margin-top: $spacing-5; }
                .post-meta {
                    position: absolute;
                    right: 0;
                    top: 0;
                    margin-top: 0.3em; } } } }

    &--highlight {
        .highlight-post {
            .post-title {
                margin-bottom: $spacing-2; }
            .media {
                margin-bottom: $spacing-2;
                background: none; } }
        .list {
            margin-top: $spacing-3;
            border-top: 1px solid var(--color-border);
            article {
                border-bottom: 1px solid var(--color-border);
                position: relative;
                padding-bottom: $spacing-3;
                margin-top: $spacing-3; } }
        article {
            > * + * {
                margin-top: $spacing-2; }
            .media {
                margin-top: 0; } }

        @include media-breakpoint-down(desktop) {
            .list {
                border-top: 1px solid var(--color-border);
                p[itemprop="articleBody"] {
                    margin-top: 0; } } }

        @include media-breakpoint-up(desktop) {
            .highlight {
                .post-title {
                    @include h2; } }
            .list {
                article {
                    .post-title {
                        @include h4; } } } }

        @include in-page-with-sidebar {
            .highlight-post {
                .post {
                    flex-direction: row;
                    .media {
                        width: columns(3);
                        margin-bottom: 0; }
                    .post-content {
                        width: columns(5);
                        margin-left: var(--grid-gutter); } } }
            .list {
                article {
                    @include grid(8, desktop, 0, 0);
                    .post-title {
                        grid-column: 1 / 7; }
                    .post-categories {
                        grid-column: 1 / 7; }
                    .post-meta {
                        grid-row: 1;
                        grid-column: 7 / 9;
                        text-align: right;
                        order: 2;
                        display: block;
                        margin-top: 0;
                        .post-author p::before {
                            content: ''; } }
                    p[itemprop="articleBody"] {
                        grid-column: 1 / 7;
                        order: 3; } } } }

        @include in-page-without-sidebar {
            .highlight {
                @include grid(2); }
            .list {
                border-top: 0;
                margin-top: 0;
                article:first-child {
                    margin-top: 0; } } } }

    &--alternate .alternate {
        .post .media img {
            aspect-ratio: unset; }

        @include media-breakpoint-up(desktop) {
            .post {
                &.right {
                    margin-left: auto; }
                &.portrait {
                    + .portrait {
                        margin-top: -30%; }
                    + .square,
                    + .landscape {
                        margin-top: -15%; }
                    + .post:not(.portrait, .square, .landscape) {
                        margin-top: -5%; } } } }

        @include media-breakpoint-down(desktop) {
            .post {
                .post-meta {
                    margin-top: $spacing-1; }
                .media {
                    margin-bottom: $spacing-2; }
                + .post {
                    margin-top: $spacing-5; } } }

        @include in-page-with-sidebar {
            .post {
                width: columns(4); } }

        @include in-page-without-sidebar {
            width: columns(10);
            margin-left: auto;
            margin-right: auto;
            .post {
                width: columns(4); } } }
    &--carousel {
        @include draggable-block;
        .container {
            padding-right: 0; }
        .carousel {
            padding-bottom: $spacing-3;
            &:hover {
                cursor: grab; }
            &.is-grabbing {
                cursor: grabbing; }
            li {
                list-style: none; }
            .posts {
                display: flex;
                gap: unset;
                .post {
                    margin: 0 calc(var(--grid-gutter) / 2); } }
            .actions-arrows {
                justify-content: space-between; } }
        @include media-breakpoint-down(desktop) {
            .carousel {
                gap: half(var(--grid-gutter));
                .post {
                    width: columns(10); }
                .grab-item:last-of-type {
                    margin-right: half(var(--grid-gutter)); }
                .actions-arrows {
                    margin-right: var(--grid-gutter); } } }
        @include media-breakpoint-up(desktop) {
            .next {
                margin-right: pxToRem(-27); } } // Marge négative pour aligner correctement le picto à la colonne
        @include in-page-with-sidebar {
            .post {
                width: columns(3);
                .post-title {
                    @include h4; } }
            .carousel {
                .actions-arrows {
                    width: offset(6); } } }
        @include in-page-without-sidebar {
            .block-content {
                display: flex;
                gap: var(--grid-gutter);
                .top {
                    width: columns(3); }
                .carousel {
                    width: columns(9); }
                .post {
                    width: columns(4); } }
            .carousel {
                .actions-arrows {
                    width: offset(8); } } } } }

// Move this part to blocks/categories when categories block is ready
.block-posts {
    .categories--grid {
        li {
            background: var(--color-background-alt);
            padding: $spacing-3;
            position: relative;
            a {
                @include h3;
                font-weight: bold;
                text-decoration: none;
                @include stretched-link; }
            p {
                display: block; } } } }
